<template>
  <div class="select-school">
    <div class="wrapper">
      <p
        v-show="!isMobile"
        class="title"
      >
        {{ $t('selectionText.hi') + ' ' + teacherName }},
        {{ $t('selectionText.pleasePick') +' '+ $t('selectionText.school') }}
      </p>
      <h1 v-show="isMobile">
        {{ $t('selectionText.hi') + ' ' + teacherName }} <br>
        {{ $t('selectionText.pleasePick') +' '+ $t('selectionText.school') }}
      </h1>
      <!-- <select v-model="selectedSchool">
        <option :value="0">Choose School</option>
        <option 
          v-for="item in teacherSchoolList" 
          :key="item.schoolId" 
          :value="item.schoolId"
        >{{ item.schoolName }}</option>
      </select> -->
      <div class="school-options">
        <!-- <div
          v-for="item in teacherSchoolList" 
          :key="item.schoolId"
          :class="{ active: selectedSchool == item.schoolId }"
          class=""
          @click="selectSchool(item.schoolId)"
        >{{ item.schoolName }}
        </div> -->
        <v-list>
          <span
            v-for="item in teacherSchoolList"
            :key="item.schoolId"
            @click="selectSchool(item.schoolId)"
          >
            <ItemList 
              :name="'schoolName'"
              :item="item" 
              :class="{ active: selectedSchool == item.schoolId }"
              :active="selectedSchool == item.schoolId"
              class="itemList pointer"
            /> 
          </span>
        </v-list>
      </div>
      <br>
      <br>
      <button 
        :disabled="selectedSchool === 0" 
        :class="{'disabled': selectedSchool === 0, 'pointer': selectedSchool !== 0}"
        @click="setActiveSchool"
      ></button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import ItemList from '@/components/ItemList.vue';
import Notifications from '../../services/notifications';

export default {
  name: 'SelectSchool',
  components: {
    ItemList
  },
  data() {
    return {
      teacherSchoolList: this.$store.getters['teacher/getSchoolList'],
      selectedSchool: 0,
      teacherName: this.$store.getters['teacher/getFirstName']
    };
  },
  computed: {
    isMobile() {
      return window.innerWidth < 720;
    }
  },
  watch: {
    selectedSchool: function() {
      console.log(this.selectedSchool);
    }
  },
  created() {
    let activeSchoolId = this.$store.getters['teacher/getActiveSchool'];

    // Check if user has only one school available to choose, and choose it automatically
    if (this.teacherSchoolList.length === 1) {
      this.selectedSchool = this.teacherSchoolList[0].schoolId;
      this.setActiveSchool();
    }

    // Check if user already selectedSchool, and continue to next page
    else if (activeSchoolId) {
      this.$router.push('/Teacher/reports');
    }
  },
  methods: {
    setActiveSchool: async function() {
      let token = this.$store.getters.getToken;

      if (this.selectedSchool != 0) {
        // Selection is valid, go on
        let data = { token: token, schoolId: this.selectedSchool };
        let newToken = await this.$store.dispatch('teacher/switchSchool', data);
        if (newToken) {
          this.$store.commit('setToken', newToken);
          this.$router.push('/teacher/reports');
        }
        // this.$store.commit('teacher/setActiveSchool', this.selectedSchool);
        // this.$router.push('/teacher');
      }
    },
    selectSchool(schoolId) {
      if (this.selectedSchool == schoolId) {
        this.selectedSchool = 0;
      } else {
        this.selectedSchool = schoolId;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import '../../assets/scss/style.scss';

.select-school {
  user-select: none;
}

.school-options {
  display: inline-grid;
  width: 100%;
}

.school-options .theme--light.v-list {
  background-color: transparent;
}

.school-single-item {
  transition: 0.3s;
  font-size: 14pt;
  border: 3px solid rgb(139, 139, 139);
  border-radius: 25px;
  padding: 5px 40px 5px 40px;
  margin: 10px;
  user-select: none;
  cursor: pointer;
}

.school-single-item:hover {
  transition: 0.4s;
  box-shadow: 0px 0px 20px -4px rgb(61, 61, 61);
}

// .active {
//   transition: 0.5s;
//   background-color: gray;
//   color: white;
//   border: 3px solid rgb(139, 139, 139);
// }

.select-school button {
  border: none;
  padding: 0.5rem 2rem 0.5rem 2rem;
  border-radius: 25px;
  font-size: 14pt;
  margin: 10px;
  box-shadow: 2px 2px 15px -5px black;
  outline: 0;
  background-color: $btns;
}

button:active {
  transition: 0.3s;
  transform: translateY(3px);
  background-color: white;
}

button.disabled {
  background-color: #cccccc;
}

h1,
.title {
  direction: rtl;
}

@media (min-width: 992px) {
  .select-school .wrapper {
    padding: 2em;
    margin-top: 5em;
    width: 45%;
    background-color: white;
    margin-left: auto;
    margin-right: auto;
    border-radius: 5px;
    box-shadow: -2px 2px 15px rgba(0, 0, 0, 0.3);
  }

  .school-options {
    width: 40%;
  }
}
</style>
