import _ from 'lodash';
import generalService from '../services/general-service';
// import TrusteeService from '../services/trustee-service';
// import { stat } from 'fs';
import stateStructures from './state-structures';
import Vue from 'vue';

// const moduleName = 'trustee';
// const setTrusteeName = `${moduleName}/setTrusteeName`;

export default {
  namespaced: true,
  state: JSON.parse(JSON.stringify(stateStructures.trusteeStateStructure)),
  getters: {
    getFirstName: state => state.firstName,
    getSchoolClassesList: state => state.schoolClasses,
    getActiveSchoolClass: state => state.activeSchoolClass,
    getMealEntryLessonNumber: state => state.activeDataEntryObject.lessonNumber,
    getMealEntryStudentData: state => state.activeDataEntryObject,
    getActiveDataEntryItems: state => state.activeDataEntryObject.items,
    getBroughtFood: function(state) {
      return state.activeDataEntryObject.broughtFood;
    }
  },
  mutations: {
    clearActiveDataEntryObject(state) {
      console.log('bla bla bla bla bla bla');
      Vue.set(state, 'activeDataEntryObject', {
        lessonNumber: null,
        studentId: null,
        firstName: null,
        lastName: null,
        broughtFood: false,
        items: {
          BR: [],
          IN: [],
          VG: [],
          FR: [],
          OT: []
        },
        score: null
      });
    },
    clearItemsArrays(state) {
      let emptyItems = {
        BR: [],
        IN: [],
        VG: [],
        FR: [],
        OT: []
      };
      Vue.set(state.activeDataEntryObject, 'items', emptyItems);
    },
    setActiveDataEntryScore(state, score) {
      // state.activeDataEntryObject.score = score;
      Vue.set(state.activeDataEntryObject, 'score', score);
    },
    setBroughtFood(state, broughtFood) {
      Vue.set(state.activeDataEntryObject, 'broughtFood', broughtFood);
    },
    addItemToActiveDataEntryItemsByCategory(state, item) {
      console.log('Adding item (store)');
      if (!state.activeDataEntryObject.items) {
        debugger;
      }
      let updatedItems = { ...state.activeDataEntryObject.items };
      let updatedArray = [
        ...state.activeDataEntryObject.items[item.category],
        item
      ];
      updatedItems[item.category] = updatedArray;
      // state.activeDataEntryObject.items[item.category].push(item);
      // state.activeDataEntryObject.items[item.category].push(item);
      Vue.set(state.activeDataEntryObject, 'items', updatedItems);
    },
    removeItemFromActiveDataEntryItemsByCategory(state, item) {
      console.log('Removing item (store)');
      let updatedItems = { ...state.activeDataEntryObject.items };
      let filtered = state.activeDataEntryObject.items[item.category].filter(
        function(itemInArray) {
          return itemInArray.itemCode !== item.itemCode;
        }
      );
      updatedItems[item.category] = filtered;
      Vue.set(state.activeDataEntryObject, 'items', updatedItems);
      // state.activeDataEntryObject.items[item.category] = filtered;
    },
    incrementActiveDataEntryScore(state, points) {
      state.activeDataEntryObject.score =
        state.activeDataEntryObject.score + points;
    },
    decreaseActiveDataEntryScore(state, points) {
      let newScore = state.activeDataEntryObject.score - points;
      if (newScore < 0) {
        state.activeDataEntryObject.score = 0;
      } else {
        state.activeDataEntryObject.score = newScore;
      }
    },
    setMealEntryLessonNumber(state, lessonNumber) {
      state.activeDataEntryObject.lessonNumber = lessonNumber;
    },
    setMealEntryStudnetDetails(state, payload) {
      state.activeDataEntryObject.studentId = payload.id;
      state.activeDataEntryObject.firstName = payload.firstName;
      state.activeDataEntryObject.lastName = payload.lastName;
      state.activeDataEntryObject.score = payload.score;
      let itemsToBeSet = payload.items || {
        BR: [],
        IN: [],
        VG: [],
        FR: [],
        OT: []
      };

      Vue.set(state.activeDataEntryObject, 'items', itemsToBeSet);
    },
    clearMetalEntryStudentObject(state) {
      state.activeDataEntryObject = {
        lessonNumber: null,
        studentId: null,
        firstName: null,
        lastName: null,
        score: null
      };
    },
    setTrusteeName(state, payload) {
      state.firstName = payload.firstName;
      state.lastName = payload.lastName;
    },
    setSchoolName(state, schoolName) {
      state.schoolName = schoolName;
    },
    setSchoolClasses(state, schoolClasses) {
      state.schoolClasses = schoolClasses;
      this.commit('trustee/mapSchoolClassesToHeb');
    },
    setDataItems(state, dataItems) {
      console.log('Trustee item collected!');
    },
    mapSchoolClassesToHeb: state => {
      state.schoolClasses = generalService.mapSchoolClassesToHeb(
        state.schoolClasses
      );
    },
    setActiveSchoolClass(state, id) {
      const foundSchoolClass = state.schoolClasses.find(
        schoolClass => schoolClass.classId == id
      );
      if (foundSchoolClass) state.activeSchoolClass = foundSchoolClass;
    }
  },
  actions: {
    populateSessionData(context, payload) {
      context.commit('setTrusteeName', payload);
      // context.commit('setSchoolName', payload.schoolName);
    },
    async setDataItems(context, token) {
      // let response = await TrusteeService.getTrusteeDummyData(token);
      // if (response.data) {
      //   context.commit('setDataItems', response.data);
      // }
    },
    cleanModuleState(context) {
      context.state = JSON.parse(
        JSON.stringify(stateStructures.trusteeStateStructure)
      );
      context.commit('clearMetalEntryStudentObject');
    }
  }
};
