import Vue from 'vue';
import Notifications from 'vue-notification';

Vue.use(Notifications);

export default {
  notify(group, title, text) {
    // console.log('default');
    Vue.notify({
      group,
      title,
      text
    });
  },
  noTitle(group, text) {
    // console.log('warn', group, text);
    Vue.notify({
      group,
      text
    });
  }
};
