<template>
  <div class="teacher-data-preview">
    <!-- <router-link :to="'teacher/'+item.id"> -->

    <div 
      class="card flex flex-row-reverse" 
      @click="goTodetail(item)"
    >
      <div class="caret flex justify-end align-center">
        <img src="../../assets/img/reports/down@3x.png" alt="">
      </div>
      <div class="text flex-column ">
        <div class="title">
          <h2>
            {{ item.name }}
          </h2>
        </div>
        <div class="content">
          {{ item.description }}
        </div>
      </div>
    </div>
    <!-- </router-link> -->
  </div>

</template>

<script>
export default {
  name: 'DataPreview',
  components: {},
  props: ['item'],
  methods: {
    goTodetail(item) {
      if(this.isTeacher){ 
        // this.$store.dispatch('teacher/setSelectedItemById', item.id);
        this.$router.push('/Teacher/reports/' + item.id);
      }else{
        this.$router.push('/trustee/reports/' + item.id);
      }

    }
  },
  computed: {
    isTeacher(){
      return this.$store.getters['isTeacher'];
    }
  }
};
</script>

<style lang="scss" scoped>


.card {
  direction: rtl;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 12px 16px 10px 10px;
  margin: 0px 10px 20px 10px;
  width: 100%;
  min-width: 300px;
  border-radius: 10px;
  background-color: #fff;
  text-align: right;
  cursor: pointer;

  .content {
    color: #828282;
  }

  &:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  }
}
</style>
