<template>
  <div class="nav-bar">

    <div 
      ref="toggleScreen" 
      class="toggle-screen" 
      @click="toggleNavMobile"
    />

    <div class="container flex space-between align-center flex-row-reverse">
      <div 
        ref="navMobile" 
        class="nav-mobile" 
        @click="toggleNavMobile"
      >
        <div class="bar1" />
        <div class="bar2" />
        <div class="bar3" />
      </div>
      <h1>
        <router-link to="/"><img 
          class="app-logo" 
          src="../assets/img/logo.png"
        ></router-link>
      </h1>

      <div class="nav-wrapper align-center flex inline-list">
        <ul 
          id="nav-desktop" 
          ref="navDesktop"
          class="nav-desktop align-center flex inline-list" 
          arial-label="Main navigation bar"
        >
          <!-- <li v-show="toDisplay.teacherLink">
            <router-link to="/teacher"> Teacher </router-link>
          </li>
          <li v-show="toDisplay.trusteeLink">
            <router-link to="/trustee"> Trustee </router-link>
          </li> -->
          <li v-show="isTeacher && activeItemName">
            <router-link to="/teacher/LessonAgendas"> {{ $t('lessonAgendas') }} </router-link>
          </li>
          <li v-show="isTeacher && activeItemName">
            <router-link to="/teacher/reports"> {{ $t('reports') }} </router-link>
          </li>
          <li v-show="isTrustee && activeItemName">
            <router-link to="/trustee/reports"> {{ $t('reports') }} </router-link>
          </li>
          <li v-show="toDisplay.contactLink">
            <router-link :to="`/${userType}/contact`"> {{ $t('contact') }} </router-link>
          </li>
          <li v-show="toDisplay.tosLink">
            <router-link to="/tos"> {{ $t('tos') }} </router-link>
          </li>
          <!-- <li v-show="toDisplay.homeLink">
            <router-link to="/"> Home </router-link>
          </li> -->
          <!-- <li v-show="toDisplay.aboutLink">
            <router-link to="about"> About </router-link>
          </li> -->
          <li>
            <button @click="logoutIdm"> Logout </button>
          </li>
        </ul>

        <div
          v-if="isUserType && activeItemName && toDisplay.selectionList" 
          class="dropdown"
        >
          <div
            class="dropBtn pointer  flex align-center"
            @click="toggleDropDown"
          >
            <img
              src="../assets/img/list-pic-1.png"
              alt=""
              class="nav-icon"
            >
            <span><b>{{ fullName }}</b> <br>
              {{ activeItemName || '' }} </span>
            <div class="arrow-wrapper">
              <img
                class="arrow"
                :class="{'down': arrowDown}"
                src="../assets/img/drop-down.png"
                alt=""
              >
            </div>
            <!--  -->
            <!-- <i class="fa fa-caret-down" /> -->
          </div>
          <div 
            ref="dropBtn" 
            class="dropdown-content"
          >
            <span v-if="items && fields">
              <span 
                v-for="item in items" 
                :key="item[fields.id]" 
                class="flex flex-row space-between"
                @click="navBarItemSwitch(item)"
              >
                <a 
                  v-if="item[fields.nameField]" 
                  href="#"
                >{{ item[fields.nameField] }} </a>
                <img
                  v-show="activeItemName === item[fields.nameField]"
                  class="check-png"
                  src="../assets/img/check.png"
                  alt=""
                >
              </span>
            </span>
          </div>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
import generalService from '../services/general-service.js';

export default {
  name: 'NavBar',
  props: ['fields', 'toDisplay'], //fields: { nameField: string, id: string}, toDisplay: { item to display (links in nav, list, etc..): boolean }
  data() {
    return {
      navBarMObile: false,
      items: [],
      activeItemName: null,
      arrowDown: false
    };
  },
  computed: {
    fullName() {
      return this.$store.getters.getFullName;
    },
    getActiveItem() {
      if (this.isTeacher) {
        return this.$store.getters['teacher/getActiveSchool'];
      } else if (this.isTrustee) {
        return this.$store.getters['trustee/getActiveSchoolClass'];
      }
      return null;
    },
    isTeacher() {
      return this.$store.getters['isTeacher'];
    },
    isTrustee() {
      return this.$store.getters['isTrustee'];
    },
    isUserType() {
      return this.$store.getters['isUserType'];
    },
    userType() {
      if (this.isTeacher) {
        return 'Teacher';
      } else if (this.isTrustee) {
        return 'Trustee';
      }
      return '';
    }
  },
  watch: {
    getActiveItem() {
      this.getActiveItemName();
    }
  },
  created() {
    this.getItems();
    this.getActiveItemName();
  },
  methods: {
    getItems() {
      if (this.isTeacher) {
        this.items = this.$store.getters['teacher/getSchoolList'];
      } else if (this.isTrustee) {
        this.items = this.$store.getters['trustee/getSchoolClassesList'];
      }
    },
    getActiveItemName() {
      let activeItem = null;
      if (this.isTeacher) {
        activeItem = this.$store.getters['teacher/getActiveSchool'];
      } else if (this.isTrustee) {
        activeItem = this.$store.getters['trustee/getActiveSchoolClass'];
      }
      if (activeItem) {
        this.activeItemName = activeItem[this.fields.nameField];
      }
    },
    toggleNavMobile() {
      this.$refs.navMobile.classList.toggle('change');
      this.$refs.navDesktop.classList.toggle('open');
      this.$refs.toggleScreen.classList.toggle('show-div');
    },
    toggleDropDown() {
      this.arrowDown = !this.arrowDown;
      this.$refs.dropBtn.classList.toggle('drop-block');
    },
    async logoutIdm() {
      await generalService.logoutIdm();
    },
    navBarItemSwitch: async function(item) {
      if (this.isTeacher) {
        let token = this.$store.getters.getToken;

        if (item) {
          // Selection is valid, go on
          let data = { token: token, schoolId: item.schoolId };
          let newToken = await this.$store.dispatch(
            'teacher/switchSchool',
            data
          );
          if (newToken) {
            this.$store.commit('setToken', newToken);
            this.toggleDropDown();
            this.$router.push('/Teacher/');
          }
        } else {
          // Selection is invalid. warn user
        }
      } else {
        //is trustee
        this.$store.commit('trustee/setActiveSchoolClass', item.classId);
        this.$router.go();
      }
    }
  }
};
</script>

<style scoped lang="scss">
@import '../assets/scss/style.scss';

// .nav-bar {
//   position: fixed;
//   width: 100%;
// }

.drop-block {
  display: block !important;
}

.full-name {
  float: left;
  color: black;
  padding-left: 14px;
}

.drop-down {
  color: black;
}

.dropdown {
  overflow: hidden;
}

.dropdown span.dropbtn.pointer {
  width: 180px;
  text-align: left;
  font-size: 16px;
  border: none;
  outline: none;
  color: black;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
  cursor: pointer;
}

.dropdown-content {
  background-color: #e03939;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 200;

  top: 7rem;
  background-color: #fff;
  display: none; // hide until menu is clicked
  list-style: none; // replace with %styled-list
  margin: 0; // replace with %styled-list
  padding: 0; // replace with %styled-list
  position: absolute;
  width: 100%;
  max-width: 220px;
}

.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
  // transition: 1s;
}

.nav-bar {
  background-color: $nav;
  padding: 5px;

  .app-logo {
    padding: 0.5rem;
  }

  a {
    color: black;
    text-decoration: none;

    &:hover {
      color: #999;
    }
  }

  h1 {
    text-decoration: none;
    margin: 0 10px;
    font-size: 1.4em;
    font-weight: bold;
    font-family: 'openSans', 'Geneva', 'Tahoma', 'sans-serif';
  }

  ul {
    margin: 0px;

    li {
      padding: 1.5rem 1rem;
      font-weight: bold;

      a {
        text-decoration: none;

        &.router-link-exact-active {
          color: #999;
        }
      }

      button {
        font-family: 'Roboto', 'OpenSans', 'Geneva', 'Tahoma', 'sans-serif';
        font-weight: bold;
        color: #fff;
        border-color: #fff;

        &:hover {
          color: #999;
          background-color: rgba(0, 0, 0, 0.5);
        }
      }
    }
  }
}

.arrow-wrapper {
  padding: 0.7em;
}

.arrow {
  transition: 0.5s;
}

.down {
  transform: rotate(180deg);
}

.check-png {
  align-self: center;
  margin-right: 1em;
}

.nav-wrapper {
  flex-direction: row-reverse;
}

.nav-mobile {
  display: none;
}
.toggle-screen {
  display: none;
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background: rgba(42, 42, 42, 0.6);
  z-index: 0;
  opacity: 0.9;
  visibility: hidden;
  cursor: pointer;
  transition: opacity 0.5s;
}

.nav-icon {
  padding-right: 0.8em;
}
@media (max-width: 720px) {
  .nav-bar .nav-wrapper #nav-desktop,
  .nav-bar .toggle-screen.show-div {
    margin-top: 4.4em;
  }

  .dropdown-content {
    top: 5rem;
    width: 100vw;
    max-width: 100%;
    right: 0;
    //  max-width=
  }

  .app-logo {
    // content: url('../assets/img/logo-mobile-header.png');
    height: 60px;
  }

  .show-div {
    z-index: 1;
    visibility: visible;
    opacity: 1;
    display: block;
  }

  .nav-wrapper {
    flex-direction: row;
  }

  .nav-desktop {
    margin: 0;
    z-index: 1;
    padding-top: 20px;
    flex-direction: column;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.9);
    border-left: 1px solid rgba(100, 100, 100, 0.5);
    min-width: 200px;
    transform: translate(100%, 0);
    transition: all 0.8s;

    &.open {
      transform: translate(0, 0);
    }

    li a {
      border-bottom: 2px solid black;
      text-decoration: dotted;
      display: block;
      line-height: 60px;
      text-align: center;
      font-size: 16px;
    }

    li a:hover {
      color: #999;
    }
  }

  /* nav-mobile button */

  .nav-mobile {
    z-index: 3;
    cursor: pointer;
    display: block;
    float: right;
    padding: 10px;
  }

  .bar1,
  .bar2,
  .bar3 {
    width: 35px;
    height: 5px;
    background-color: black;
    margin: 6px 0;
    transition: 0.4s;
  }

  /* Rotate first bar */
  .change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }

  /* Fade out the second bar */
  .change .bar2 {
    opacity: 0;
  }

  /* Rotate last bar */
  .change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }
}
</style>
