import Vue from 'vue';
import Vuex from 'vuex';
// import Cookies from 'js-cookie';
import sessionConfig from './config/session-config';
import VuexPersistence from 'vuex-persist';
import teacher from './store-modules/teacher';
import trustee from './store-modules/trustee';
import reports from './store-modules/reports';

const moment = require('moment');

Vue.use(Vuex);

const vuexCookie = new VuexPersistence({
  restoreState: key => {
    let localStorageData = JSON.parse(window.localStorage.getItem(key));
    if (localStorageData) {
      let stateExpirationTime = moment(localStorageData.stateSaveTime).add(
        sessionConfig.STATE_STORAGE_SAVE_DURATION_IN_MINUTES,
        'minutes'
      );
      if (moment(new Date()).isBefore(stateExpirationTime)) {
        return localStorageData;
      } else {
        localStorage.removeItem(key);
        return null;
      }
    } else {
      return null;
    }
  },
  saveState: (key, state) => {
    state.stateSaveTime = moment(new Date());
    console.log(state.stateSaveTime);
    window.localStorage.setItem(key, JSON.stringify(state));
  }
});

export default new Vuex.Store({
  modules: {
    teacher,
    trustee,
    reports
  },
  state: {
    token: null,
    language: 'he',
    userType: null,
    logoutUriPrefix: null,
    stateSaveTime: null,
    showSpinner: false
  },
  mutations: {
    startSpinner(state) {
      // Vue.set(state, 'showSpinner', true);
      state.showSpinner = true;
    },
    stopSpinner(state) {
      // Vue.set(state, 'showSpinner', false);
      state.showSpinner = false;
    },
    setToken(state, token) {
      state.token = token;
    },
    setName(state, data) {
      if (!data) {
        this.commit('trustee/setTrusteeName', {
          firstName: null,
          lastName: null
        });
        this.commit('teacher/setTeacherName', {
          firstName: null,
          lastName: null
        });
      } else {
        let userType = data.userType;
        let fullName = {
          firstName: data.firstName,
          lastName: data.lastName
        };
        if (userType === 'T') {
          this.commit('teacher/setTeacherName', fullName);
          this.commit('trustee/setTrusteeName', {
            firstName: null,
            lastName: null
          });
        } else {
          this.commit('trustee/setTrusteeName', fullName);
          this.commit('teacher/setTeacherName', {
            firstName: null,
            lastName: null
          });
        }
      }
    },
    setUserType(state, type) {
      state.userType = type;
    },
    setUserSchools(state, data) {
      if (data) {
        if (data.userType === 'T') {
          // if (data.userType === 'T') {
          this.commit('teacher/setSchoolsList', data.schools);
          // }
        } else {
          this.commit('trustee/setSchoolName', data.schoolName);
          this.commit('trustee/setSchoolClasses', data.schoolClasses);
        }
      } else {
        this.commit('teacher/setSchoolsList', null);
        this.commit('trustee/setSchoolName', null);
      }
    },
    setLanguage(state, language) {
      state.language = language;
    },
    setLogoutUriPrefix(state, logoutUriPrefix) {
      state.logoutUriPrefix = logoutUriPrefix;
    }
  },
  actions: {
    setLoginData(state, data) {
      // First clean state
      this.dispatch('cleanState');

      // Commit new state
      this.commit('setToken', data.token);
      this.commit('setLogoutUriPrefix', data.logoutUriPrefix);
      this.commit('setName', data);
      this.commit('setUserType', data.userType);

      this.commit('setUserSchools', data);
      // Fetch dataItems & assign schools
      if (data.userType === 'T') {
        this.commit('teacher/setSchoolClasses', data.schoolClasses);
        this.dispatch('teacher/setDataItems', data.token);
        this.commit('teacher/setTos', data.showTos);
      } else {
        this.dispatch('trustee/setDataItems', data.token);
      }
    },
    cleanState(state) {
      this.commit('setToken', null);
      this.commit('setName', null);
      this.commit('setUserType', null);
      this.commit('setUserSchools', null);
      this.dispatch('teacher/cleanModuleState');
      this.dispatch('trustee/cleanModuleState');
    }
  },
  getters: {
    getSpinnerState(state) {
      console.log('show spinner called!!!!');
      console.log(state.showSpinner);
      return state.showSpinner;
    },
    isTeacher(state) {
      return state.userType === 'T';
    },
    isTrustee(state) {
      return state.userType === 'S';
    },
    isUserType(state) {
      return state.userType;
    },
    getToken(state) {
      return state.token;
    },
    getState(state) {
      return state;
    },
    getFullName(state) {
      let fullName = null;
      if (state.userType === 'T') {
        fullName = `${state.teacher.firstName || ''} ${state.teacher.lastName ||
          ''}`.trim();
      } else {
        fullName = `${state.trustee.firstName || ''} ${state.trustee.lastName ||
          ''}`.trim();
      }
      return fullName;
    }
  },
  plugins: [vuexCookie.plugin]
});

function minutesToMilliseconds(minutes) {
  let convertMinutesToMilliseconds = minutes * 60 * 1000;
  return convertMinutesToMilliseconds;
}
