<template>
  <modal
    name="custom"
    :draggable="draggable || false"
    :width="width"
    :height="'auto'"
  >
    <div
      class="modal"
      style="text-align:center;"
    >
      <h2>title here</h2>
      <div>
        content
      </div><br>
      <div>
        <button
          style="width:100%;"
          @click="$modal.hide('custom')"
        >close</button>
      </div>
    </div>
  </modal>
</template>

<script>
export default {
  props: ['width', 'height'],
  data() {
    return {
      draggable: true
    };
  }
};
</script>
