import Vue from 'vue';
import Router from 'vue-router';
import Login from './views/Login.vue';
import IdmCallback from './views/IdmCallback.vue';
import SelectSchool from './components/teacher/SelectSchool.vue';
import DataDetails from './components/reports/DataDetails.vue';
import ReportsWrapper from './components/reports/Reports.wrapper.vue';
import SelectSchoolClass from './components/trustee/SelectSchoolClass.vue';
import Notifications from './components/Notifications.example.vue';
import store from './store';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/idmcallback',
      name: 'IdmCallback',
      component: IdmCallback
    },
    {
      path: '/idmcallback/:declaredPathSuffixForIdm',
      // name: 'idmcallback',
      component: IdmCallback,
      props: true
    },
    {
      path: '/About',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'about' */ './views/About.vue')
    },
    {
      path: '/tos',
      name: 'tos',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'tos' */ './views/TermsOfService.vue')
    },
    {
      path: '/upload',
      name: 'ExcelUpload',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'ExcelUpload' */ './views/ExcelUpload.vue')
    },
    {
      path: '/json-upload',
      name: 'JsonUpload',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'ExcelUpload' */ './views/JsonUpload.vue')
    },
    {
      path: '/smt',
      name: 'StudentManagment',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'ExcelUpload' */ './views/StudentManagment.vue')
    },
    {
      path: '/Trustee',
      // name: 'trustee',

      component: () =>
        import(/* webpackChunkName: 'trustee' */ './views/Trustee.vue'),
      //dev
      beforeEnter: (to, from, next) => {
        let token = store.getters.getToken;
        let isTrustee = store.getters.isTrustee;
        if (token && isTrustee) {
          next();
        } else {
          next('/');
        }
      },
      children: [
        {
          path: '/',
          name: 'selectSchoolClass',
          component: SelectSchoolClass
        },
        {
          path: '/Trustee/LessonAgendas',
          name: 'LessonAgendas',
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () =>
            import(/* webpackChunkName: 'contact' */ './components/trustee/LessonAgendas.vue')
        },
        {
          path: '/Trustee/SelectStudent',
          name: 'SelectStudent',
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () =>
            import(/* webpackChunkName: 'contact' */ './components/trustee/SelectStudent.vue')
        },
        {
          path: '/Trustee/MealDataEntry',
          name: 'MealDataEntry',
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () =>
            import(/* webpackChunkName: 'contact' */ './components/trustee/MealDataEntry.vue')
        },
        {
          path: '/Trustee/contact',
          name: 'trusteeContact',
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () =>
            import(/* webpackChunkName: 'contact' */ './views/Contact.vue')
        },
        {
          path: '/Trustee/reports',
          name: 'trusteeReports',
          component: ReportsWrapper
        },
        ,
        {
          path: '/Trustee/reports/:itemId',
          name: 'details',
          component: DataDetails
        }
      ]
    },
    {
      path: '/tos',
      name: 'tos',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: 'tos' */ './views/TermsOfService.vue')
    },
    {
      path: '/Teacher',
      component: () =>
        import(/* webpackChunkName: 'teacher' */ './views/Teacher.vue'),
      beforeEnter: (to, from, next) => {
        let token = store.getters.getToken;
        let isTeacher = store.getters.isTeacher;
        if (token && isTeacher) {
          if (to.name === 'selectSchool') {
            next();
          } else {
            // Check if already selected 'Active School'
            let activeSchool = store.getters['teacher/getActiveSchool'];
            if (activeSchool) {
              // activeSchool exist
              next();
            } else {
              // no activeSchool selected
              next('/Teacher');
            }
          }
        } else {
          next('/');
        }
      },
      children: [
        {
          path: '/',
          name: 'selectSchool',
          component: SelectSchool
        },
        {
          path: '/Teacher/LessonAgendas',
          name: 'lesson-agendas',
          component: () =>
            import(/* webpackChunkName: 'contact' */ './components/teacher/LessonAgendas.vue')
        },
        {
          path: '/Teacher/reports',
          name: 'reports-wrapper',
          component: ReportsWrapper
        },
        {
          path: '/Teacher/reports/:itemId',
          name: 'details',
          component: DataDetails
        },
        // {
        //   path: '/Teacher/About',
        //   name: 'about',
        //   // route level code-splitting
        //   // this generates a separate chunk (about.[hash].js) for this route
        //   // which is lazy-loaded when the route is visited.
        //   component: () =>
        //     import(/* webpackChunkName: 'about' */ './views/About.vue')
        // },
        {
          path: '/Teacher/contact',
          name: 'teacherContact',
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () =>
            import(/* webpackChunkName: 'contact' */ './views/Contact.vue')
        }
      ]
    },
    {
      path: '/notificationsExample',
      name: 'notificationsExample',
      component: Notifications
    },
    {
      path: '*',
      name: '404',

      component: () =>
        import(/* webpackChunkName: 'ma-taim-404' */ './views/404.vue')
    }
  ]
});
