<template>
  <div class="data-category-one">
    <!-- <h1>Teacher</h1> -->
    <!-- will show only a list if: mobile or trustee desktop. -->
    <div v-show="!desktop || ($store.getters.isTrustee && desktop)"> 
      <DataList v-if="items" />
      <div v-else>No data for you.</div>
    </div>
    <!-- will show both (DataDetails contains DataList) if not trustee and desktop mode -->
    <div v-show="desktop && !$store.getters.isTrustee">
      <DataDetails />
    </div>
  </div>
</template>

<script>
import DataList from '@/components/reports/DataList.vue';
import DataDetails from '@/components/reports/DataDetails.vue';

export default {
  name: 'Reports',
  components: {
    DataList,
    DataDetails
  },
  data() {
    return {
      items: null
    };
  },
  computed: {
    desktop: () => window.innerWidth > 768
  },
  created() {
    this.loadItems();
  },
  methods: {
    loadItems() {
      // this.items = this.$store.getters['teacher/queryToData'];
      this.items = true;
    }
  }
};
</script>

