import axios from 'axios';
import apiConfig from './app-api';
//import stubs from '../util/stubs';
import store from '../store';
import logger from './logger';
import idmConfig from '../config/idm-config';
import Notifications from '../services/notifications';

const grades = {
  '1': '',
  '2': '',
  '3': '',
  '4': '',
  '5': '',
  '6': ''
};

export default {
  // This is temporary, until IDM is in place
  performMockLogin: async function(userData) {
    const response = await axios
      .post(
        apiConfig.getMockLoginUrl(),
        { userData },
        {
          headers: { 'Content-Type': 'application/json' }
        }
      )
      .catch(e => Notifications.noTitle('error', `Login failed`));
    return response.data;
    // await stubs.stubDelay();
    // return {
    //     id: 1,
    //     dummyData: "dummy login data"
    // }
  },
  openIdLogin: async function(callBackUrl, explicitRedirectUri = null) {
    const response = await axios.post(
      apiConfig.getIdmLoginUrl(),
      explicitRedirectUri
        ? { redirectedUrl: callBackUrl, explicitRedirectUri }
        : { redirectedUrl: callBackUrl },
      { headers: { 'Content-Type': 'application/json' } }
    );
    return response.data;
  },
  logoutIdm: async function() {
    let logoutUrl = `${idmConfig.logoutURL}${window.location.origin}`;
    console.log(window.location);
    console.log(logoutUrl);
    await store.dispatch('cleanState');
    window.location.href = logoutUrl;
  },
  startOpenIdSession: async function(explicitRedirectUri = null) {
    const response = await axios.post(
      apiConfig.getStartOpenIdSessionUrl(),
      explicitRedirectUri ? { explicitRedirectUri } : {},
      { headers: { 'Content-Type': 'application/json' } }
    );
    logger.debug('response data from login: ');
    logger.debug(response.data);
    return response.data;
  },
  sendContactUsMail: async function(mailData) {
    const response = await axios.post(apiConfig.getMockMailUrl(), mailData, {
      headers: { 'Content-Type': 'application/json' }
    });
    return response.data;
  },
  sendTosConfirmation: async function(token) {
    const response = await axios.post(apiConfig.getMockAcceptTosUrl(), null, {
      headers: {
        'Content-Type': 'application/json',
        'x-access-token': token
      }
    });
    return response.data;
  },
  getMockIdmJSON: async function() {
    const response = await axios.get(apiConfig.getMockIdmJSON(), null, {
      headers: {
        'Content-Type': 'application/json'
      }
    });
    return response.data;
  },
  mapSchoolClassesToHeb: schoolClasses => {
    // debugger;
    return schoolClasses.map(schoolClass => ({
      ...schoolClass,
      schoolClassName:
        grades[schoolClass.classGrade] + "' " + schoolClass.classNumber
    }));
  }
};
