<template>
  <div class="filter-form">
    <div class="inputs-wrapper">
      <div class="datepicker-wrapper">
        <Datepicker
          :label="$t('date') + ' ' + $t('start')" 
          :icon="true"
          placeholder="start" 
          :min="`${new Date().getFullYear()}/09/02`" 
          :date2.sync="dates.from"
          :max="dates.to || new Date()"
        />
      </div>
      <div class="datepicker-wrapper">
        <Datepicker
          :label="$t('date') + ' ' + $t('end')" 
          :date2.sync="dates.to"
          placeholder="end" 
          :min="dates.from" 
          :max="new Date()"
        />
      </div>
    </div>
    <hr>
    <div
      v-show="$store.getters.isTeacher"
      class="inputs-wrapper"
    >
      <div class="select-wrapper">
        <v-select
          v-model="schoolClassId"
          prepend-icon="people"
          :items="items"
          item-text="display"
          item-value="value"
          :placeholder="$t('selectionText.schoolClass')"
          :label="$t('selectionText.schoolClass')"
        />
      </div>
      <hr class="selects-hr">
      <div class="select-wrapper">
        <v-select
          v-model="student"
          prepend-icon="person"
          :items="studentsBySchoolClass"
          :disabled="$store.getters.isTrustee"
          item-text="display"
          item-value="value"
          :placeholder="$t('allStudents')"
          :label="$t('allStudents')"
        />
      </div>
    </div>
    <div>
      <button
        class="btn-form"
        :disabled="validateForm"
        :class="{ disabled: validateForm }"
        @click="initReportsFilter"
      >
        {{ $t('confirm') }}
      </button>
    </div>
  </div>
</template>

<script>
import Datepicker from '@/components/Datepicker.vue';
import reportsService from '../../services/reports-service.js';
import generalService from '../../services/general-service.js';
import notifications from '../../services/notifications.js';
import util from '../../util/util.js';
import teacherService from '../../services/teacher-service.js';

export default {
  components: {
    Datepicker
  },
  data() {
    return {
      dates: {
        from: `${new Date().getFullYear()}-09-01`,
        to: new Date().toISOString().substr(0, 10)
      },
      student: {
        // this will be either this obj, or a studentObjId (string). the v-model binds only the value when a user selects a student.
        display: this.$t('allStudents'),
        value: 'allStudents'
      },
      studentsBySchoolClass: [
        {
          display: this.$t('allStudents'),
          value: 'allStudents'
        }
      ],
      //if trustee, the class for filter should be the active class
      schoolClassId: this.$store.getters.isTrustee
        ? this.$store.getters['trustee/getActiveSchoolClass'].classId
        : null,
      reports: []
    };
  },
  computed: {
    validateForm() {
      return (
        new Date(this.dates.from) < new Date(this.dates.to) &&
        this.schoolClassId === null &&
        this.student
      );
    },
    items() {
      return this.$store.getters['teacher/getSchoolClasses'].map(
        schoolClass => ({
          display: schoolClass.schoolClassName,
          value: schoolClass.classId
        })
      );
    }
    // students() {
    //   let arr = this.mapStudentsToSelect([
    //     ...this.$store.getters['reports/students']
    //   ]);
    //   //should return a list of students
    //   arr.unshift({
    //     display: this.$t('allStudents'),
    //     value: 'allStudents'
    //   });
    //   return arr;
    // }
  },
  watch: {
    schoolClassId() {
      if (this.$store.getters.isTeacher) {
        this.refreshSchoolClass();
      }
    },
    schoolClassId(val) {
      this.refreshSchoolClass(val);
    }
  },
  methods: {
    initReportsFilter() {
      if (this.student.value === 'allStudents') {
        return this.filterStudentsReportsByDate(
          util.deepClone(this.$store.getters['reports/students'])
        );
      }
      return this.filterStudentReportsByDate(
        this.fetchStudentById(this.student)
      );
    },
    filterStudentsReportsByDate(students) {
      students.forEach(
        async student =>
          (student.reports = await reportsService.filterByDate(
            student.reports,
            'learnedDate',
            this.dates.from,
            this.dates.to
          ))
      );
      // console.log(students, 'all students report');
      return students;
    },
    async filterStudentReportsByDate(student) {
      let x = await reportsService.filterByDate(
        student.reports,
        'learnedDate',
        this.dates.from,
        this.dates.to
      );
      // console.log(x, '1 student report');
      return [x];
    },
    fetchStudentById(id) {
      let studentObj = [...this.$store.getters['reports/students']].find(
        student => student.studentObjId === id
      );
      if (!studentObj) {
        return notifications.noTitle('warn', 'no student was found!');
      }
      return studentObj;
    },
    async refreshSchoolClass(classId) {
      let token = this.$store.getters.getToken;
      let students = await teacherService.getStudentsByClassId(token, classId);

      // console.log(
      //   'refresh schoolClass here after selecting another class',
      //   students
      // ); // debug
      this.studentsBySchoolClass = [
        {
          display: this.$t('allStudents'),
          value: 'allStudents'
        },
        ...this.mapStudentsToSelect(students)
      ];
    },
    studentToDefault() {
      this.student = {
        //default is all students.
        display: this.$t('allStudents'),
        value: 'allStudents'
      };
    },
    mapStudentsToSelect(students) {
      return students.map(student => ({
        display: `${student.firstName} ${student.lastName}`,
        value: student.studentObjId || student._id
      }));
    }
  }
};
</script>

<style lang="scss">
@import '../../assets/scss/style.scss';
.filter-form {
  button.disabled {
    background-color: #cccccc;
  }

  .select-wrapper {
    direction: rtl;

    .v-select__selection--comma,
    input {
      margin-right: 1em;
    }

    .primary--text.theme--light,
    .v-icon.material-icons.theme--light {
      color: rgba(0, 0, 0, 0.4) !important;
    }

    .v-select__slot {
      .v-icon {
        visibility: hidden;
      }
    }

    // .v-text-field>.v-input__control>.v-input__slot:after, .v-text-field>.v-input__control>.v-input__slot:before {
    //     border: none;
    // }
  }

  .btn-form {
    border: none;
    border-radius: 30px;
    // background-color: rgb(194, 194, 194);
    background-color: $btns;
    padding: 1rem 3rem 1rem 3rem;
    margin-bottom: 0.5em;
    margin-top: 6em;
  }

  .v-list__tile__title,
  .v-text-field--placeholder {
    text-align: center;
  }
}

#trustee {
  .inputs-wrapper {
    display: block;
  }

  .datepicker-wrapper,
  .select-wrapper {
    width: auto;
  }

  hr {
    display: none;
  }

  h1 {
    text-align: center;
  }
}

//-------------------------------desktop only
@media (min-width: 720px) {
  .inputs-wrapper {
    display: flex;
    justify-content: space-around;
    flex-direction: row-reverse;
    margin-bottom: 4em;
    margin-top: 3em;
  }

  .filter-form {
    label.v-label {
      left: auto !important;
      right: -0.2em !important;
      top: -0.5em;
    }

    .datepicker-wrapper,
    .select-wrapper {
      width: 13vw;
    }
  }

  .selects-hr {
    display: none;
  }
}

//-------------------------------------mobile only
#trustee {
  .filter-form {
    label.v-label {
      display: none;
    }

    .v-text-field > .v-input__control > .v-input__slot:before,
    .v-text-field > .v-input__control > .v-input__slot:after {
      border: none;
    }
  }
}

@media (max-width: 720px) {
  .filter-form {
    .v-text-field > .v-input__control > .v-input__slot:before,
    .v-text-field > .v-input__control > .v-input__slot:after {
      border: none;
    }

    label.v-label {
      display: none;
    }
  }
}
</style>
