<template>
  <div class="select-school">
    <div class="wrapper">
      <h1>
        {{ `${$t('selectionText.hi')} ${trusteeName}` }} <br>
        {{ $t('selectionText.pleasePick') +' '+ $t('selectionText.schoolClass') }}
      </h1>
      <!-- <select v-model="selectedSchoolClassId">
        <option :value="0">Choose School</option>
        <option 
          v-for="item intrusteeSchoolList" 
          :key="item.schoolId" 
          :value="item.schoolId"
        >{{ item.schoolName }}</option>
      </select> -->
      <div class="school-options">
        <!-- <div
          v-for="item intrusteeSchoolList" 
          :key="item.schoolId"
          :class="{ active: selectedSchoolClassId == item.schoolId }"
          class=""
          @click="selectSchool(item.schoolId)"
        >{{ item.schoolName }}
        </div> -->
        <v-list>
          <span
            v-for="item in trusteeClassesList"
            :key="item.classId"
            @click="selectSchoolClass(item.classId)"
          >
            <ItemList 
              :name="'schoolClassName'"
              :item="item" 
              :class="{ active: selectedSchoolClassId == item.classId }"
              :active="selectedSchoolClassId == item.classId"
              class="itemList pointer"
            /> 
          </span>
        </v-list>
      </div>
      <br>
      <br>
      <button 
        :disabled="selectedSchoolClassId === 0" 
        :class="{'disabled': selectedSchoolClassId === 0}"
        @click="setActiveSchoolClass()"
      > {{ $t('confirm') }} </button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import ItemList from '@/components/ItemList.vue';

export default {
  name: 'SelectSchool',
  components: {
    ItemList
  },
  data() {
    return {
      trusteeClassesList: this.$store.getters['trustee/getSchoolClassesList'],
      selectedSchoolClassId: 0,
      trusteeName: this.$store.getters['trustee/getFirstName']
    };
  },
  computed: {
    isMobile() {
      return window.innerWidth < 720;
    }
  },
  watch: {
    selectedSchoolClassId: function() {
      console.log(this.selectedSchoolClassId);
    }
  },
  created() {
    // let activeSchoolId = this.$store.getters['trustee/getActiveSchoolClass'];
    // Check if user has only one school available to choose, and choose it automatically
    // if (this.trusteeSchoolList.length === 1) {
    //   this.selectedSchoolClassId = this.trusteeSchoolList[0].schoolId;
    //   this.setActiveSchool();
    // }
    // Check if user already selectedSchoolClassId, and continue to next page
    // else if (activeSchoolId) {
    //   this.$router.push('/trustee/trusteeDataCategoryOne');
    // }
  },
  methods: {
    setActiveSchoolClass() {
      if (this.selectedSchoolClassId != 0) {
        // Selection is valid, go on
        // this.$router.push('/trustee/trusteeDataCategoryOne');
        this.$store.commit(
          'trustee/setActiveSchoolClass',
          this.selectedSchoolClassId
        );
        // TODO: send the user to correct route (agenda selection)
        this.$router.push('/Trustee/LessonAgendas');
      }
    },
    selectSchoolClass(id) {
      if (this.selectedSchoolClassId == id) {
        this.selectedSchoolClassId = 0;
      } else {
        this.selectedSchoolClassId = id;
      }
    }
  }
};
</script>

