  user-select: none;
}

.school-options {
  display: inline-grid;
  width: 100%;
}

.school-options .theme--light.v-list {
  background-color: transparent;
}

.school-single-item {
  transition: 0.3s;
  font-size: 14pt;
  border: 3px solid rgb(139, 139, 139);
  border-radius: 25px;
  padding: 5px 40px 5px 40px;
  margin: 10px;
  user-select: none;
}

.school-single-item:hover {
  transition: 0.4s;
  box-shadow: 0px 0px 20px -4px rgb(61, 61, 61);
}

// .active {
