//   background-color: gray;
//   color: white;
//   border: 3px solid rgb(139, 139, 139);
// }

.select-school button {
  border: none;
  padding: 0.5rem 2rem 0.5rem 2rem;
  border-radius: 25px;
  font-size: 14pt;
  margin: 10px;
  box-shadow: 2px 2px 15px -5px black;
  outline: 0;
  background-color: $btns;
}

button:active {
  transition: 0.3s;
  transform: translateY(3px);
  background-color: white;
}

button.disabled {
  background-color: #cccccc;
}

.select-school h1,
.title {
  direction: rtl;
}

.select-school h1 {
  color: black;
}

