<template>
  <div class="datepicker">
    <div id="datepicker-container">
      <v-app>
        <!-- <v-layout row wrap> -->
        <v-dialog
          ref="dialog"
          v-model="modal"
          :return-value.sync="date"
          persistent
          lazy
          full-width
          width="290px"
        >
          <v-text-field
            slot="activator"
            v-model="dateFormatted"
            :placeholder="$t('date') +' '+ $t(placeholder)"
            prepend-icon="event"
            :class="{'hide-icon': !icon }"
            readonly
            :label="label"
          />
          <v-date-picker
            v-model="date"
            scrollable
            locale="he"
            color="#4cdef6"
            header-color="white"
            :max="new Date(max).toISOString().substr(0, 10)"
            :min="new Date(min).toISOString().substr(0, 10)"
          >
            <v-spacer />
            <button
              :disabled="date == null"
              :class="{ disabled: date == null }"
              class="btn-form"
              @click="$refs.dialog.save(date)"
            >
              {{ $t('confirm') }}
            </button>

          </v-date-picker>
        </v-dialog>
        <!-- </v-layout> -->
      </v-app>
    </div>
  </div>
</template>

<script>
export default {
  props: ['icon', 'max', 'min', 'date2', 'placeholder', 'label'], //PL: string  (placeholder) = end / start
  data() {
    return {
      date: new Date(this.date2).toISOString().substr(0, 10),
      dateFormatted: this.formatDate(this.date2) || null,
      modal: false
    };
  },
  watch: {
    date(val) {
      this.$emit('update:date2', this.date);
      this.dateFormatted = this.formatDate(this.date);
    }
  },

  methods: {
    formatDate(date) {
      if (!date) return null;
      // console.log({ date });
      return new Date(date).toLocaleDateString('he', {
        month: 'long',
        day: 'numeric',
        year: 'numeric'
      });
    }
  }
};
</script>

<style lang="scss">
@import '../assets/scss/style.scss';

#datepicker-container {
  // direction: rtl !important;
  .v-picker.v-card {
    border-radius: 7px;
  }

  .v-date-picker-title__date {
    text-align: center;
    color: #4cdef6;
    font-size: 110%;
    font-weight: 600;
  }

  .v-date-picker-title__year {
    color: black;
  }

  .v-input.v-text-field input {
    margin-right: 1em;
  }

  .v-date-picker-table table,
  .v-input.v-text-field {
    direction: rtl;
  }

  // .v-text-field>.v-input__control>.v-input__slot:before, .v-text-field>.v-input__control>.v-input__slot:after  {
  //     border: none;
  // }

  .application--wrap {
    min-height: 0;
  }

  .theme--light.application {
    background: transparent;
  }

  .btn-form {
    border: none;
    border-radius: 20px;
    // background-color: rgb(194, 194, 194);
    background-color: $btns;
    padding: 0.5rem 2rem 0.5rem 2rem;
    margin: 0.5em 0;
  }

  .v-card__actions {
    display: inline;
  }

  .btn-form.disabled {
    background-color: #cccccc;
  }
}

//trustee desktop, looks like mobile
#trustee {
  #datepicker-container {
    .v-text-field > .v-input__control > .v-input__slot:before,
    .v-text-field > .v-input__control > .v-input__slot:after {
      border: none;
    }

    label.v-label {
      display: none;
    }

    .hide-icon .v-icon {
      visibility: hidden;
    }
  }
}

//-------------------------------------mobile only
@media (max-width: 720px) {
  #datepicker-container {
    .v-text-field > .v-input__control > .v-input__slot:before,
    .v-text-field > .v-input__control > .v-input__slot:after {
      border: none;
    }

    label.v-label {
      display: none;
    }

    .hide-icon .v-icon {
      visibility: hidden;
    }
  }
}

//-------------------------------------desktop only
@media (min-width: 720px) {
  #datepicker-container {
    label.v-label {
      left: auto !important;
      right: -0.2em !important;
      top: -0.5em;
    }
  }
}
</style>
